*{
    margin: 0; padding: 0;
}
#app{
    position: relative;
}
a {
    color: #666;
    text-decoration: none;
    outline: none;
}
body, td, input, textarea, select, button {
    color: #666;

    font-family: "Microsoft Yahei", "Lucida Grande", Verdana, Lucida, Helvetica, Arial, sans-serif;
}
ul,ol,li{
    list-style: none;
}
.clearfix{
    display: block;
    content: '';
    clear:both;
    height: 0;
}
.clearfix:after{
  zoom: 1;
}
.barBox{
    width:100%;
    position: fixed;
    left: 0;
    bottom: 0;
    background: #ccc;
}
.barBox li{
    width:20%;
    float:left;
    font-size: .3rem;
    text-align: center;
    height: 1rem;
    line-height: 1rem;
}
.active{ color: red; }
.router-link-active{
    color: red;
}


