.category-box {
    width: 100%;
    min-width: 1210px;
    height: 40px;
    background: #fff;
}
.w1210 {
    width: 1210px;
    margin: 0px auto;
}
.category-box .home-category {
    height: 40px;
    position: relative;
    width: 210px;
    z-index: 103;
}
.category-box .home-category .bgColor_xbc {
    background: rgba(63, 171, 164, 1) !important;
}

.category-box .home-category .menu-event {
    color: #fff;
    display: block;
    text-align: center;
    text-decoration: none;
    width: 210px;
    height: 40px;
    line-height: 40px;
    z-index: 9999;
    position: absolute;
    top: 0px;
    left: 0;
    font-size: 16px;
    background: #3A3A3A;
}
.category-box .all-category {
    line-height: 38px;
    text-align: center;
    padding: 0;
    height: 40px;
    width: 990px;
    position: relative;
    margin-left: 10px;
    overflow: hidden;
}
.category-box .all-category ul {
    display: block;
    width: 100%;
    height: 40px;
}
.category-box .all-category li {
    text-decoration: none;
    float: left;
    z-index: 10;
    position: relative;
}
.category-box .all-category li a {
    color: #3C3C3C;
    height: 38px;
    line-height: 38px;
    display: inline-block;
    text-decoration: none;
    font-size: 16px;
    padding: 0 20px;
}
.fl {
    float: left;
}
.category-box .all-category li a.nav:hover, .category-box .all-category li a.current {
    color: #17C4BB !important;
}
.login_right {
    float: right;
    background: #fff;
    width: 248px;
    height: 443px;
    border: 1px solid #E1E1E1;
    border-top: none;
    position: absolute;
    top: 0px;
    z-index: 999;
}
.denglu {
    height: 85px;
    background-color: #F8F8F8;
}
.denglu dl {
    padding: 10px 20px;
}
.denglu dl dd {
    width: 65px;
    height: 65px;
    float: left;
    margin-right: 20px;
    background: #EFEFEF;
    border-radius: 65px;
}
.denglu dl dt {
    width: 123px;
    float: left;
}
.immediately {
    border: 1px solid #000000;
    line-height: 20px;
    padding: 2px 2px;
    border-radius: 3px;
    color: #474747;
    font-size: 12px;
}
.my_xw {
    margin: 3px 10px 0px 10px;
}
.m_l li {
    float: left;
    text-align: center;
    width: 48%;
    line-height: 35px;
    font-size: 14px;
}

.m_dj {
    background: #fff;
    height: 36px;
}
.my_xw p {
    line-height: 28px;
    width: 226px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.category-layer {
    border-top: none;
    height: 443px;
    width: 210px;
    position: relative;
    top: 40px;
    z-index: 9;
}
.bg-color2 {
    background: #26344B;
}
.category-layer .list {
    height: 49px;
}
.category-layer .list .cat {
    height: 49px;
    position: relative;
    width: 210px;
    z-index: 9;
    color: #ffffff;
}
.category-layer .list .cat .cat-name {
    height: 49px;
    line-height: 49px;
    overflow: hidden;
    position: absolute;
    font-weight: 400;
    font-size: 14px;
    padding: 0 10px 0px 15px;
    display: block;
}
.category-layer .list .cat .cat-name a {
    color: #ffffff;
    text-decoration: none;
}
.category-layer .list .cat i {
    position: absolute;
    top: 9px;
    right: 14px;
    width: 4px;
    height: 14px;
    color: #ffffff;
    font: 400 12px/14px consolas;
}
.ban_mian {
    height: auto;
    margin: 0px auto;
    position: relative;
    padding-top: 10px;
}
.home-focus-layout {
    width: 100%;
    height: 443px;
    position: relative;
    z-index: 1;
}
.full-screen-slides {
    width: 100%;
    height: 443px;
    position: relative;
    z-index: 1;
}
.full-screen-slides li {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
}
.banner_img{

    background:url(../assets/banner1.jpg) center top no-repeat scroll rgb(242, 242, 242);
    display: list-item; 
    z-index: 800;
}
.x_mainChild1 {
    width: 1210px;
    margin: 0 auto;
    margin-bottom: 20px;
}
.x_imgL {
    width: 192px;
    height: 205px;
    background: green;
}
.x_imgTab {
    width: 518px;
    height: 205px;
    background: orange;
    margin-left: 13px;
    overflow: hidden;
}
.x_imgR {
    width: 474px;
    height: 205px;
    background: black;
    margin-left: 13px;
}
.x_mainChild2 {
    width: 1210px;
    margin: 0 auto;
    margin-bottom: 20px;
    overflow: hidden;
}
.x_mainTitle {
    width: 100%;
}
.x_mainTitle h2 {
    font-size: 20px;
    font-family: "";
    font-weight: bold;
    line-height: 40px;
    padding-top: 20px;
    padding-bottom: 10px;
    color: #333;
}
.x_storBox {
    width: 1220px;
}
.x_storBox li {
    border: 1px solid #ddd;
    width: 293px;
    height: 351px;
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
}
.x_storBox li a {
    display: block;
    width: 100%;
    height: 100%;
}
.x_storChild {
    width: 100%;
    height: 100%;
}
.x_scTop {
    padding: 4px;
}
.x_scTop img {
    width: 282px;
    height: 211px;
    vertical-align: middle;
}
.x_scCenter {
    height: 35px;
    line-height: 35px;
    text-align: center;
    background-color: #ededed;
    color: #666;
}
.x_scBottom {
    width: 100%;
}
.x_xwj {
    font-size: 14px;
    padding: 0 15px;
    color: #999;
}
.x_xwj strong {
    font-size: 28px;
    color: #fc4c67;
    margin: 0 10px 0 5px;
}

strong {
    font-weight: bold;
}
.x_xwj span {
    background: url(../assets/lt.jpg) no-repeat left center;
    height: 16px;
    line-height: 0px;
    float: right;
    display: inline-block;
    padding-left: 20px;
    padding-top: 15px;
}
.lyqlj {
    width: 1200px;
    overflow: hidden;
    margin: 20px auto;
}
.lyqlj .l_tit {
    height: 30px;
    border-bottom: 1px #d9d5d6 solid;
    font-size: 16px;
    margin-bottom: 14px;
}
.lyqlj ul {
    width: 1230px;
}
.lyqlj ul li {
    float: left;
    width: 140px;
  
    height: 50px;
    border-right: 1px #dadada solid;
    
    padding: 0 16px;
    text-align: center;
    overflow: hidden;
}
.lyqlj ul li img {
    _position: relative;
    _top: -8px;
    width: 100%;
    height: 100%;
}


