.registerBox{
    width:100%;
    height: 13.3rem;
    background: url(../imgs/res.jpg);
    background-size:100% 100%;
}
.log{
    margin-top: .5rem;
}
.txt{
    font-size: .4rem;
    float: left;
    height: 1.3rem;
    line-height: 1.3rem;
    padding-left: .3rem;
    color: #fff;
}
.pc{
    float: left;
    width: 1.3rem;
    height: 1.3rem;
    border-radius: 50%;
    background: url(../imgs/logo_1.png) no-repeat .14rem;
    background-size: 1rem 1rem;
    margin-left: 1.5rem;
}
.resbox{
    height: 0.92rem;
    width: 100%;
   margin-top: 3rem;

}
.acumb{
    width: 6rem;
    margin-left: .7rem;
    height: 100%;
    margin-top: .5rem;
    border-bottom: 1px solid #ccc;  
}
.typeName,.typecode{
    float: left;
    font-size: .3rem;
    color: #fff;
    width: 1.5rem;
    height: 0.92rem;
    line-height: 0.92rem;
    padding-left: .3rem;
}
.typeValue{
    float: left;
    width: 4rem;
    outline: none;
    border: 0px;
    _border-bottom: 1px solid #ccc;
    background: transparent;
    height: 0.92rem;
    border-radius: 0;
    color: #fff;
}
.typeValueCode{
    float: left;
    width: 2rem;
    outline: none;
    border: 0px;
    _border-bottom: 1px solid #ccc;
    background: transparent;
    height: 0.92rem;
    border-radius: 0;
    color: #fff;
}
.code{
    float: left;
    font-size: .3rem;
    width: 1.5rem;
    height: 0.9rem;
    line-height: 0.92rem;
    border-left: 1px solid #ccc;
    padding-left: .3rem;
}

.agrment{
    font-size: .3rem;
    color: #038bd5;
}
.check{
    margin-left: .7rem;
}
.registBtn{
    display: block;
    width: 5.82rem;
    height: 0.95rem;
    line-height: 0.95rem;
    text-align: center;
    font-size: .3rem;
    color: #fff;
    margin-left: 0.8rem;
    margin-top: .6rem;
    padding-left: .2rem;
    border-radius: .5rem;
    background: #ff8587;
   
}


