.site-footer {
    width: 100%;
    min-width: 1210px;
    font-size: 14px;
    line-height: 1.5em;
    margin-top: 20px;
    position: relative;
    z-index: 999;
}
.footer-related {
    width: 100%;
    min-width: 1210px;
    padding: 40px 0 0px;
    background: #3A3A3A;
    color: #CCC;
}
.site-footer .footer-article {
    min-height: 140px;
    height: auto;
    overflow: hidden;
}
.site-footer dl.col-article-first {
    margin-left: 0;
}
.site-footer dl.col-article {
    float: left;
    width: 190px;
    text-indent: 40px;
    overflow: hidden;
}
.site-footer dl.col-article dt {
    margin-bottom: 10px;
    font-size: 16px;
}
.site-footer dl.col-article dd {
    margin: 0 0 6px;
    font-size: 12px;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: inline-block;
    overflow: hidden;
    width: 190px;
}
.footer-info {
    width: 100%;
    min-width: 1210px;
    height: auto;
    padding: 25px 0 15px;
    font-size: 12px;
    background: #3A3A3A;
    color: #cccccc;
    display: inline-block;
    position: relative;
    z-index: 101;
}
.footer-info .info-text {
    width: 1210px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 10px;
}
.site-footer dl.col-article dd a:hover {
    color: #E31939;
}
.site-footer dl.col-article dd a {
    color: #ccc;
}


