#top {
    height: 30px;
    line-height: 30px;
    background: #f7f7f7;
    border-bottom: 1px #eeeeee solid;
}
#top .top_main {
    height: 30px;
    line-height: 30px;
    margin: 0 auto;
    position: relative;
    z-index: 12;
}
.Width {
    width: 1200px;
    margin: 0 auto;
}
#top .top_main .shouc {
    padding-left: 16px;
    background:url(../assets/top.png) no-repeat left 2px;
}
#top .top_main .top_nav {
    height: 30px;
    position: absolute;
    right: 0px;
    top: 0px;
}
.fr {
    float: right;
}
#top .top_main .top_nav .i_nav {
    padding: 0px 12px;
    float: left;
    background: url(../assets/top_1.png) no-repeat right center;
}
#top .top_main .top_nav .muen {
    width: 60px;
    position: relative;
    z-index: 1;
}

#top .top_main .top_nav .i_nav {
    padding: 0px 12px;
    float: left;
    background: url(../assets/top_1.png) no-repeat right center;
}
.header {
    width: 100%;
    min-width: 1210px;
    height: 90px;
    padding: 20px 0px 10px;
    background: #fff;
    margin: 0 auto;
}
.w1210 {
    width: 1210px;
    margin: 0px auto;
}
.logo-info {
    width: 280px;
    float: left;
}
.logo-info a.logo {
    display: block;
    width: auto;
    max-width: 180px;
    height: 80px;
    line-height: 80px;
    overflow: hidden;
}
.logo-info a.logo img {
    max-width: 100%;
    max-height: 100%;
    vertical-align: middle;
}
.header .search {
    width: 550px;
    position: relative;
    z-index: 1;
    float: left;
    margin-left: 110px;
}
.header .search .search-form .search-info {
    background: #fff;
    height: 64px;
    position: relative;
}
.header .search .search-form .search-info .search-type {
    width: 100%;
    height: 25px;
    position: absolute;
    top: 0px;
    left: 0px;
}
.header .search .search-form .search-info .search-box {
    height: 34px;
    position: absolute;
    left: 0px;
    top: 25px;
    border: solid rgba(63, 171, 164, 1);
    border-width: 2px 0 2px 2px;
}
.header .search .search-form .search-info .search-box .search-box-con {
    height: 30px;
    line-height: 30px;
}
.header .search .search-form .search-info .button {
    position: absolute;
    top: 25px;
    right: 0px;
    width: 100px;
    height: 38px;
    line-height: 38px;
    border: 0;
    font-size: 16px;
    letter-spacing: 4px;
    cursor: pointer;
    color: #fff;
    overflow: hidden;
    padding: 0px 10px !important;
}
.header .search .search-form .search-info .search-type li {
    width: 50px;
    height: 25px;
    line-height: 25px;
    float: left;
    text-align: center;
    cursor: pointer;
}
.header .search .search-form .search-info #keyword {
    color: #999;
    margin: 0;
    position: relative;
    z-index: 2;
    width: 440px;
    height: 24px;
    line-height: 24px;
    padding: 5px 3px 5px 5px;
    outline: 0;
    border: 0;
}
.bg-color {
    background: rgba(63, 171, 164, 1);
}


