
.loginbox{
    width:100%;
    height: 13.3rem;
    background: url(../imgs/bg1.jpg);
    background-size:100% 100%;
}
.logo{
    text-indent: -999px;
    background: url(../imgs/logo_1.png) no-repeat 50% 20%;
    height: 1.3rem;
    background-size: 1rem 1rem;
    padding-top: .5rem;
}
h2{
    font-size: .4rem;
    text-align: center;
    color: #fff;
}
.formInput{
    width: 100%;
    height: 3rem;
}
.login,.register{
    width: 6.82rem;
    margin-left: 0.33rem;
    color:#fff;
    font-size: .4rem;
    margin-top: .6rem;
    position: relative;
}
.logintxt,.registertxt{
    width: 5.82rem;
    height: 0.95rem;
    border: none;
    outline: none;
    opacity: 0.5;
    background: #fff;
    margin-left: 0.4rem;
    padding-left: .2rem;
    border-radius: .5rem;
    font-size: .3rem;
}
.number{
    width: 0.32rem;
    height: 0.42rem;
    background: url(../imgs/p.png) no-repeat;
    position: absolute;
}
.forpass{
    display: block;
    margin: .3rem 0 0 5rem;
    font-size: .3rem;
    color:#ff8587;
}
.loginBtn{
    display: block;
    width: 5.82rem;
    height: 0.95rem;
    line-height: 0.95rem;
    text-align: center;
    font-size: .3rem;
    color: #fff;
    margin-left: 0.8rem;
    margin-top: .6rem;
    padding-left: .2rem;
    border-radius: .5rem;
    background: #ff8587;
    -webkit-tap-highlight-color:rgba(255,255,255,0)
}
.registerBtn{
    display: block;
   
    text-align: center;
    font-size: .3rem;
    color: #ff8587;
    margin-left: 0.2rem;
    margin-top: .4rem;
    padding-left: .2rem;
   
}


